"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { useAuth } from "@/contexts/AuthContext";
import { toast } from "sonner";
import { Shield, Loader2 } from "lucide-react";
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs";

export default function AuthPage() {
  const { signIn, signUp, user, isLoading: authLoading } = useAuth();
  const router = useRouter();

  const [activeTab, setActiveTab] = useState<"login" | "signup">("login");
  const [isLoading, setIsLoading] = useState(false);

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  if (user && !authLoading) {
    router.replace("/");
    return null;
  }

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    try {
      const { error } = await signIn(email, password);
      if (error) {
        toast.error("Invalid email or password");
        return;
      }
      toast.success("Welcome back!");
      router.push("/");
    } finally {
      setIsLoading(false);
    }
  };

  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      toast.error("Passwords do not match");
      return;
    }
    setIsLoading(true);
    try {
      const { error } = await signUp(email, password, name);
      if (error) {
        toast.error(error.message);
        return;
      }
      toast.success("Account created successfully!");
      router.push("/");
    } finally {
      setIsLoading(false);
    }
  };

  if (authLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-slate-50">
        <Loader2 className="h-8 w-8 animate-spin text-indigo-600" />
      </div>
    );
  }

  return (
    <div className="min-h-screen flex">

      {/* LEFT BRAND SECTION */}
      <div className="hidden lg:flex w-1/2 bg-gradient-to-br from-indigo-600 to-indigo-900 items-center justify-center p-12">
        <div className="text-center space-y-6 text-white">
          <div className="w-24 h-24 rounded-3xl bg-white/10 flex items-center justify-center mx-auto shadow-xl">
            <Shield className="w-12 h-12 text-white" />
          </div>
          <h1 className="text-4xl font-bold">ServiceDesk</h1>
          <p className="text-indigo-100 max-w-md text-lg">
            Streamline your support workflow and manage service requests
            efficiently.
          </p>
        </div>
      </div>

      {/* RIGHT FORM SECTION */}
      <div className="w-full lg:w-1/2 bg-slate-50 flex items-center justify-center p-6">
        <div className="w-full max-w-md bg-white rounded-2xl shadow-xl border border-slate-200 p-8 relative animate-fade-in">

          {/* TOGGLE */}
          <div className="absolute top-6 right-6 text-sm flex gap-1">
            <span className="text-slate-500">
              {activeTab === "login"
                ? "No account?"
                : "Have an account?"}
            </span>
            <button
              onClick={() =>
                setActiveTab(activeTab === "login" ? "signup" : "login")
              }
              className="text-indigo-600 font-semibold hover:underline"
            >
              {activeTab === "login" ? "Sign up" : "Sign in"}
            </button>
          </div>

          {/* MOBILE HEADER */}
          <div className="lg:hidden flex flex-col items-center mb-8">
            <div className="w-12 h-12 bg-indigo-600 rounded-xl flex items-center justify-center mb-3">
              <Shield className="w-6 h-6 text-white" />
            </div>
            <h2 className="text-xl font-bold text-slate-900">
              ServiceDesk
            </h2>
          </div>

          <Tabs value={activeTab} onValueChange={(v) => setActiveTab(v as any)}>

            {/* LOGIN */}
            <TabsContent value="login" className="space-y-6">
              <div>
                <h3 className="text-2xl font-bold text-slate-900">
                  Welcome back
                </h3>
                <p className="text-slate-500">
                  Sign in to your account
                </p>
              </div>

              <form onSubmit={handleLogin} className="space-y-4">
                <input
                  type="email"
                  placeholder="Email address"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                  className="w-full px-4 py-3 border border-slate-200 rounded-lg shadow-sm focus:ring-2 focus:ring-indigo-600 focus:border-transparent"
                />

                <input
                  type="password"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  className="w-full px-4 py-3 border border-slate-200 rounded-lg shadow-sm focus:ring-2 focus:ring-indigo-600 focus:border-transparent"
                />

                <button
                  type="submit"
                  disabled={isLoading}
                  className="w-full py-3 bg-indigo-600 text-white font-bold rounded-lg hover:bg-indigo-700 transition disabled:opacity-70"
                >
                  {isLoading ? (
                    <Loader2 className="h-5 w-5 animate-spin mx-auto" />
                  ) : (
                    "Sign In"
                  )}
                </button>
              </form>
            </TabsContent>

            {/* SIGNUP */}
            <TabsContent value="signup" className="space-y-6">
              <div>
                <h3 className="text-2xl font-bold text-slate-900">
                  Create account
                </h3>
                <p className="text-slate-500">
                  Get started with ServiceDesk
                </p>
              </div>

              <form onSubmit={handleSignup} className="space-y-3">
                <input
                  type="text"
                  placeholder="Full name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  required
                  className="w-full px-4 py-3 border border-slate-200 rounded-lg shadow-sm focus:ring-2 focus:ring-indigo-600"
                />

                <input
                  type="email"
                  placeholder="Email address"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                  className="w-full px-4 py-3 border border-slate-200 rounded-lg shadow-sm focus:ring-2 focus:ring-indigo-600"
                />

                <input
                  type="password"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  className="w-full px-4 py-3 border border-slate-200 rounded-lg shadow-sm focus:ring-2 focus:ring-indigo-600"
                />

                <input
                  type="password"
                  placeholder="Confirm password"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  required
                  className="w-full px-4 py-3 border border-slate-200 rounded-lg shadow-sm focus:ring-2 focus:ring-indigo-600"
                />

                <button
                  type="submit"
                  disabled={isLoading}
                  className="w-full py-3 bg-indigo-600 text-white font-bold rounded-lg hover:bg-indigo-700 transition disabled:opacity-70"
                >
                  {isLoading ? (
                    <Loader2 className="h-5 w-5 animate-spin mx-auto" />
                  ) : (
                    "Create Account"
                  )}
                </button>
              </form>
            </TabsContent>

          </Tabs>

          {/* FOOTER */}
          <div className="mt-8 flex justify-center gap-6 text-xs text-slate-400">
            <a href="#" className="hover:text-slate-600">
              Terms of Service
            </a>
            <a href="#" className="hover:text-slate-600">
              Privacy Policy
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
